.header {
  padding: 39px 0;

  background-color: $color-default-white;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .header__wrapper{
      display: flex;
      align-items: center;
     
      .main-nav{
  
        .main-nav__link:last-child{
          padding-left: 40px;
        }
  
        .main-nav__link:hover{
          color: $color-default-orange;
        }
  
      }
      .header__phone{
        display: flex;
        align-items: center;
  
        padding-left: 53px;
  
        color: $color-default-orange;
        text-decoration: underline;
      }
    }
  }
}
